<template>
  <div id="Video">
    <div class="input">
      <input type="text">
    </div>
    <div class="MVplayer">
      <div class="videoPlay iconfont icon-bofang" @click="videoPlay"></div>
      <video class="video" src='../assets/video/pian.mp4' @ended="ended" @timeupdate='playTime'></video>
      <!-- <video class="video" :src='videoUrl' @ended="ended" @timeupdate='playTime'></video> -->
      <div class="controls">
        <div class="main">
          <!--  -->
          <div v-if="playIcon === true" class="suspend iconfont icon-bofang1" @click="suspend"></div>
          <div v-else class="suspend iconfont icon-24gf-pause2" @click="suspend"></div>
          <!--  -->
          <div class="progress">
            <div class="upper" @click="lower"></div>
            <div class="lower" @click="lower"></div>
          </div>
          <!-- / -->
          <div class="timer">
            <span v-if="playTimer === ''">00:00:00</span><span>{{this.playTimer}}</span> / <span v-if="totalTime === ''">00:00:00</span><span>{{this.totalTime}}</span>
          </div>
          <!-- / -->
          <div class="whole iconfont icon-quanping"></div>
        </div>
      </div>
    </div>
    <div class="BG01">

    </div>
    <div class="BG02">

    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      videoUrl: '/media/pian.19dce68a.mp4',
      // 
      playIcon: true,
      // 
      totalTime: '',
      // 
      playTimer: ''
    }
  },
  created () {
  },
  methods: {
    // 
    suspend () {
      var video = $('.video').get(0)
      this.playIcon = !this.playIcon
      if (this.playIcon === false) {
        this.videoPlay()
      } else {
        video.pause()
      }
    },
    // 
    videoPlay () {
      $('.videoPlay').hide()
      var video = $('.video').get(0)
      this.totalTime = this.formatTime(video.duration)
      this.playIcon = false
      video.play()
    },
    // 
    formatTime (time) {
      // 
      // 
      // 1 60*60 == 3600
      //  4000s   4000/3600 = 1
      var h = Math.floor(time / 3600)
      //  4000s   4000%3600 = 400 400/60=
      var m = Math.floor(time % 3600 / 60)
      //  4000s   4000%60 =
      var s = Math.floor(time % 60)
      return (h < 10 ? '0' + h : h) + ':' + (m < 10 ? '0' + m : m) + ':' + (s < 10 ? '0' + s : s)
    },
    // 
    playTime () {
      var video = $('.video').get(0)
      // upper   /  * 100 + %
      var w = video.currentTime / video.duration * 100 + '%'
      // console.log(w)
      $('.upper').css('width', w)
      //  
      this.playTimer = this.formatTime(video.currentTime)
    },
    ended () {
      // this.playTimer = ''
      this.playIcon = true
      // $('.upper').css('width', 0)
    },
    // 
    lower () {
      var video = $('.video').get(0)
      //   / lower * 
      var offset = event.offsetX
      // console.log(offset)
      // 
      var current = offset / $('.lower').width() * video.duration
      console.log(current)
      //    currentTime
      video.currentTime = Math.floor(current)
    }
  }
}
</script>

<style src='../assets/css/05.video.css' scoped></style>
<style scoped>
#Video{
  padding-top: 80px;
}
#Video .BG01{
  width: 1100px;
  height: 100vh;
  margin: 0 auto;
  margin-top: 225px;
  background: url(../assets/images/001.jpg) no-repeat center center;
  background-attachment: fixed;
}
#Video .BG02{
  width: 1100px;
  height: 100vh;
  margin: 0 auto;
  background: url(../assets/images/002.png) no-repeat center center;
  background-attachment: fixed;
}
</style>
