<template>
  <div id="shopping">
    <!--  -->
    <div class="shoppingHead">
      <div class="shoppingLogo">
        <img src="../assets/images/logo.jpeg" alt="logo">
      </div>
      <div class="goodsTotal"></div>
    </div>
    <!--  -->
    <div class="shoppingBody">
      <!--  -->
      <div class="SPB_top">
        <div class="t-checkbox">&nbsp;</div>
        <div class="t-goods"></div>
        <div class="t-props">&nbsp;</div>
        <div class="t-price"></div>
        <div class="t-quantity"></div>
        <div class="t-sum"></div>
        <div class="t-action"></div>
      </div>
      <!--  -->
      <div class="SPB_cen">
        <div class="SPB_items" v-for="(item, index) in shoppingList" :key="index">
          <div class="p-checkbox">
            <div class="SPB_index" v-if="(index + 1) < 10">0{{index +1}}</div>
            <div class="SPB_index" v-else>{{index +1}}</div>
            <div class="SPB_input"><input type="checkbox" name="" id=""
            v-model="getCommoditySelected[item.id]"
            :disabled='item.num === 0'
            @change='selectClick(item.id, getCommoditySelected[item.id])'></div>
          </div>
          <div class="p-img">
            <img :src="item.image" alt="">
          </div>
          <div class="p-goods">{{item.sell_point}}</div>
          <div class="p-props">{{item.content}}</div>
          <div class="p-price">
            <span> </span><span>{{item.price}}</span>
          </div>
          <div class="p-quantity">
            <numbox @PDcount='countChanged' :max='item.num'
            :initCount='getCommodityCount[item.id]'
            :PDid='item.id'></numbox>
          </div>
          <div class="p-sum">
            <span> </span>
            <span>666</span>
          </div>
          <div class="p-ops">
            <span class="p-ops-a" @click="removeShop(item.id)"></span>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="SPB_tail">
        <div class="select-all">
          <input type="checkbox" name="" id="" checked>
          <span>&nbsp;&nbsp;</span>
        </div>
        <div class="ta-quantity">
          <span> </span><span style="color: red">{{this.getSelectedCount}}</span><span> </span>
        </div>
        <div class="ta-action">
          <div class="ta_a_pric"><span></span><span style="color: red">&nbsp;</span><span style="color: red;font-size: 20px;">{{CalculatePrice}}</span></div>
          <div class="ta_a_settlement" @click="toSettlement"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import numbox from '../components/smallAssembly.vue'
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'shopping',
  components: {
    numbox
  },
  data () {
    return {
      // 
      shoppingList: []
    }
  },
  computed: {
    // mapStateshopcartcar
    ...mapState('shopcart', ['car']),
    // vuex shopcartgetters     ,   ,    
    ...mapGetters('shopcart', ['getCommodityCount', 'getCommoditySelected', 'getSelectedCount', 'getSelectedCommdity']),
    // 
    CalculatePrice () {
      const selectList = this.getSelectedCommdity
      let totalPrice = 0
      this.shoppingList.forEach(item => {
        if (selectList[item.id]) {
          totalPrice += item.price * selectList[item.id].count
        }
      })
      return totalPrice
    }
  },
  created () {
    // 
    this.getProductList()
    // console.log(this.getSelectedCommdity)
  },
  methods: {
    // 
    getProductList () {
      // console.log(this.car)
      const idArr = []
      // carid
      this.car.forEach(item => {
        idArr.push(item.id)
      })
      // console.log(idArr)
      // 
      this.$http.get('shopcart', {
        params: {
          ids: idArr
        }
      }).then(res => {
        // console.log(res)
        this.shoppingList = res.data.data
        this.shoppingList.forEach(item => {
          if (item.num === 0) {
            // flase
            this.selectClick(item.id, false)
          }
        })
      })
    },
    // 
    countChanged (val) {
      // console.log(val)
      this.$store.commit('shopcart/updataShopcart', val)
    },
    // 
    selectClick (id, selected) {
      // vue
      this.$store.commit('shopcart/updataShopSelected', {
        id: id,
        selected: selected
      })
      console.log('' + selected)
    },
    // 
    removeShop (id) {
      // vuexlocalStorage
      this.$store.commit('shopcart/removeShopcart', id)
      //  shoppingList 
      this.shoppingList.forEach((item, index) => {
        if (parseInt(item.id) === parseInt(id)) {
          this.shoppingList.splice(index, 1)
        }
      })
    },
    // 
    toSettlement () {
      //  0
      if (this.getSelectedCount <= 0) {
        alert('')
      } else {
        // vuex shopcart buy
        this.$store.commit('shopcart/getShopbuy')
        // 
        this.$router.push('/shopping/settlement')
      }
    }
  }
}
</script>

<style src='../assets/shoppingcss/shoppingcss.css' scoped></style>
<style scoped>
#shopping{
  padding-top: 80px;
}
</style>
