<template>
  <div id="my">
    <div class="myorder">
      <!--  -->
      <div class="order">
        <div class="myMsg">
          <div class="myImg"><img src="../assets/images/sjmv.jpg" alt=""></div>
          <span>{{$store.state.user.username}}</span>
        </div>
        <div class="title">
          <div><span></span></div>
          <div><span></span></div>
          <div><span></span></div>
          <div><span></span></div>
          <div class="spanEnd"><span ></span></div>
        </div>
        <div class="items" v-for="(item, index) in setList" :key="index">
          <div v-if="(index + 1) < 10">0{{index+1}}</div>
          <div v-else>{{index+1}}</div>
          <div>&nbsp;<span style="color: rgb(88, 8, 235)">{{item.address_name}}</span>&nbsp;</div>
          <div class="items_site"><span>:</span><span>{{item.address_area}}</span><span>{{item.address_detail}}</span></div>
          <div>
            <span class="viewOrder" @click="boxShow(item.id)"></span>
            <span class="cancel" style="margin-left: 10px;cursor: pointer;" @click.once="cancelOrder(item.id, index)"></span>
          </div>
        </div>
        <div class="animation"><span class="icon vehicle iconfont icon-dahuoche"></span></div>
      </div>
      <!--  -->
      <div class="times">
        <div class="times_t">&nbsp;</div>
        <div class="times_b">
          <div class="number">{{this.dates}}</div>
          <div class="week">{{day}}</div>
          <div class="specificDate">{{this.year}}-{{this.month}}</div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="orderDetails" :class="{orderDetailsShow: isorderDetailsShow}">
      <form action="#">
        <div class="icon iconfont icon-guanbi" @click="boxHide"></div>
        <div class="forms">
          <div class="forms_top">
            <span></span>
            <div class="address_area"></div>
            <div class="address_detail"></div>
          </div>
          <div class="forms_in">
            <span></span>
            <div class="address_tel">13688888888</div>
          </div>
          <div class="forms_bom">
          <span></span><span class="address_name">{{this.showLists.address_name}}</span><span></span>
          <div class="goods_items" v-for="(item, index) in showLists" :key="index">
            <div class="goods_name"></div>
            <div class="goods_price">&nbsp;&nbsp;999</div>
            <div class="goods_count">x&nbsp;1</div>
          </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      // 
      setList: [],
      // 
      showLists: [],
      // 
      year: '',
      // 
      month: '',
      // 
      dates: '',
      // 
      day: '',
      // 
      isorderDetailsShow: false,
      cancel: true
    }
  },
  created () {
    // 
    this.setOrderList()
    // 
    this.newData()
    // console.log(this.showLists)
  },
  methods: {
    // 
    setOrderList () {
      this.$http
        .get('order/list')
        .then(res => {
          // console.log(res)
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            this.setList = res.data.data
          }
        })
    },
    // 
    newData () {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      //   +1
      var dates = date.getDate()
      var day = date.getDay()
      var arr = ['', '', '', '', '', '', '']
      this.year = year
      this.month = month
      this.dates = dates
      this.day = arr[day]
    },
    // 
    boxShow (Id) {
      console.log(Id)
      this.isorderDetailsShow = true
      this.$http
        .get('order/show', { id: Id })
        .then(res => {
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            console.log(res)
            this.showLists = ''
            this.showLists = res.data.data.user_order_goods
            console.log(this.showLists)
          }
        })
    },
    // 
    boxHide () {
      this.isorderDetailsShow = false
    },
    // 
    cancelOrder (ID, index) {
      this.$http
        .post('order/cancel', { id: ID })
        .then(res => {
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            alert(res.data.msg)
            // console.log($('.cancel')[index].innerHTML)
            $('.cancel')[index].innerHTML = ''
          }
        })
    }
  }
}
</script>

<style src='../assets/shoppingcss/my.css' scoped></style>
<style scoped>
#my{
  width: 100%;
  padding-top: 80px;
}
</style>
